@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");
:root {
  --color-black: #000;
  --color-white: #fff;
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  background: #F1F6FA;
  letter-spacing: -0.015em;
  color: #0A243F;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

:root {
  --color-red: rgba(235, 87, 87, 1);
  --color-dark: #0A243F;
  --color-grey: #8B90A0;
  --color-blue: #0F47C0;
}

body, html {
  height: 100%;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

body {
  min-width: 320px;
}

.main {
  flex: 1;
}

.wrap {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1410px;
}

@media (max-width: 1450px) {
  .wrap {
    max-width: 1310px;
  }
}
@media (max-width: 374px) {
  .wrap {
    padding: 0 15px;
  }
}
.btn-outline-primary {
  border: 1px solid #D7D6DC;
}

.tabs__item {
  display: none;
}

.tabs__item:first-child {
  display: block;
}

.tabs__item.active {
  display: block;
}

.header {
  padding: 16px 0;
  height: 80px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 5;
  background: #F1F6FA;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
}

.app {
  padding-top: 80px;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  font-family: "Cabin", sans-serif;
  font-size: 20px;
  font-weight: 700;
  color: #1d4289;
  display: flex;
  align-items: center;
  margin-right: 25px;
}
.header__logo img {
  height: 40px;
  margin-right: 5px;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__search {
  position: relative;
}
.header__search button {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 35px;
  height: 35px;
  border: none;
  background-color: transparent;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 100%;
}
.header__search button:hover {
  background-color: #F1F6FA;
}

.header__search input {
  background: #FFFFFF;
  border-radius: 15px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
  height: 48px;
  padding-left: 72px;
  padding-right: 15px;
  font-size: 14px;
  line-height: 20px;
  border: 0;
  width: 604px;
}

.header__right {
  display: flex;
  align-items: center;
}

.avatar {
  width: 48px;
  height: 48px;
  border-radius: 100%;
  overflow: hidden;
}
.avatar img {
  height: auto;
}

.header__right__btn {
  margin: 0 4px;
  width: 35px;
  height: 35px;
  padding: 5px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s all;
  cursor: pointer;
}

.header__right__btn:hover {
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}

.person__menu {
  display: flex;
  align-items: center;
  border: 0;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
}

.person__menu__v {
  margin-left: 5px;
}

.person {
  margin-left: 4px;
  position: relative;
}

.person__nav {
  position: absolute;
  top: calc(100% + 6px);
  right: 0;
  background: #FFFFFF;
  border: 1px solid #D7D6DC;
  box-sizing: border-box;
  box-shadow: 5px 4px 9px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  padding: 12px 12px;
  width: 273px;
  display: none;
}
.person__nav ul {
  margin: 12px 0;
  padding: 0;
}
.person__nav ul li {
  padding: 0;
  margin-bottom: 14px;
  list-style: none;
}
.person__nav ul li a {
  color: #0A243F;
  font-size: 16px;
  display: block;
  transition: 0.3s all;
}
.person__nav ul li a:hover {
  color: #092C9E;
}
.person__nav ul li:last-child {
  margin-bottom: 0;
}

.person__nav__line {
  height: 2px;
  background: #D7D6DC;
}

.person.open .person__nav {
  display: block;
}
.person.open .person__menu__v {
  transform: rotateX(180deg);
}

.sub-header {
  background: #262A39;
  padding-top: 8px;
  margin-bottom: 20px;
}

.sub-header__nav ul {
  display: flex;
  padding: 0;
  margin: 0;
}
.sub-header__nav li {
  margin-right: 34px;
  list-style: none;
}
.sub-header__nav a {
  display: block;
  padding: 12px 32px;
  font-weight: bold;
  font-size: 18px;
  line-height: 28px;
  color: #FFFFFF;
  border-radius: 10px 10px 0px 0px;
  text-align: center;
}
.sub-header__nav a:hover {
  background: #F0F2F5;
  color: #022964;
}
.sub-header__nav a.active {
  background: #F0F2F5;
  color: #022964;
}

@media (min-width: 1350px) {
  .sub-header__nav {
    margin-left: -32px;
  }
}
@media (max-width: 1200px) {
  .header__search input {
    width: 400px;
  }
}
@media (max-width: 991px) {
  .header__search input {
    width: 300px;
  }

  .header__search button {
    left: 15px;
  }

  .header__search input {
    padding-left: 50px;
  }

  .sub-header__nav a {
    padding: 12px;
    font-size: 16px;
    line-height: 19px;
  }
}
.header__right__btn--search {
  color: #092C9E;
  border: none;
  background-color: transparent;
  display: none;
}
.header__right__btn--search .close {
  display: none;
}
.header__right__btn--search .open {
  display: block;
}

.header__right__btn--search.active .close {
  display: block;
}
.header__right__btn--search.active .open {
  display: none;
}

@media (max-width: 767px) {
  .header__right__btn--search {
    display: flex;
  }

  .header__search {
    position: fixed;
    top: 72px;
    right: 20px;
    left: 20px;
    transform: translateX(100%);
    opacity: 0;
    transition: 0.3s all;
  }
  .header__search input {
    width: 100%;
  }

  .header__search.active {
    transform: translateX(0);
    opacity: 1;
  }

  .header {
    padding: 11px 0;
    height: 70px;
  }

  .app {
    padding-top: 70px;
  }

  .sub-header {
    overflow: auto;
  }

  .sub-header__nav ul {
    flex-wrap: nowrap;
  }

  .sub-header__nav a {
    white-space: nowrap;
    font-size: 14px;
  }

  .sub-header__nav li {
    margin-right: 15px;
  }
}
@media (max-width: 575px) {
  .header__logo {
    font-size: 18px;
  }

  .header__right__btn {
    margin: 0 10px;
  }

  .header__logo {
    margin-right: 15px;
  }

  .avatar {
    height: 40px;
    width: 40px;
  }

  .header {
    padding: 15px 0;
  }
}
@media (max-width: 374px) {
  .header__right__btn {
    width: 30px;
    height: 30px;
  }
}
.header__right__btn.have-mess {
  position: relative;
}
.header__right__btn.have-mess .have-mess__text {
  position: absolute;
  right: 0;
  bottom: 0;
  transform: translate(5px, -2px);
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: #092C9E;
}

.header__right__btn.have-mess:hover {
  box-shadow: none;
  background-color: transparent;
}

.have-mess.tooltips-wrap .tooltips {
  display: none;
  bottom: 0;
  left: auto;
  transform: translateX(0%);
  width: 215px;
  width: max-content;
  right: 100%;
  padding-right: 10px;
}

.have-mess.tooltips-wrap .tooltips:before {
  left: auto;
  right: -8px;
  bottom: 10px;
  top: auto;
}

@media (max-width: 767px) {
  .header__right__btn--onlyds {
    display: none;
  }
}
@media (max-width: 374px) {
  .header__right__btn.have-mess .have-mess__text {
    right: -1px;
  }

  .have-mess.tooltips-wrap .tooltips {
    font-size: 11px;
  }
}
.person__nav__action {
  display: flex;
  margin: 0 -10px;
  margin-bottom: 10px;
}

.footer {
  padding-top: 120px;
  padding-bottom: 27px;
  background: #262A39;
  margin-top: 120px;
}

.footer__wrap {
  display: flex;
  justify-content: space-between;
  padding-bottom: 52px;
}

.footer__right__nav {
  display: flex;
}
.footer__right__nav .f-col {
  margin-left: 123px;
  display: flex;
  flex-direction: column;
}
.footer__right__nav .title {
  color: #F1F6FA;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
}
.footer__right__nav ul {
  margin: 0;
  padding: 0;
}
.footer__right__nav li {
  list-style: none;
  margin-bottom: 4px;
}
.footer__right__nav a {
  color: #F1F6FA;
  font-size: 14px;
  line-height: 20px;
}
.footer__right__nav a:hover {
  text-decoration: underline;
}
.footer__right__nav .last-li {
  margin-top: 52px;
}

@media (max-width: 767px) {
  .footer__right__nav ul {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .footer__right__nav .last-li {
    margin-top: 0;
    flex: 1;
    display: flex;
    align-items: flex-end;
  }
  .footer__right__nav .last-li a {
    display: block;
  }
}
.footer__wrap__bot {
  border-top: 1px solid #F4F7FF;
  padding-top: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__copy {
  font-size: 15px;
  line-height: 24px;
  color: #F4F7FF;
  padding-right: 25px;
}

.footer__soc {
  display: flex;
  align-items: center;
}
.footer__soc ul {
  display: flex;
  padding: 0;
  margin: 0;
}
.footer__soc li {
  list-style: none;
  margin-left: 10px;
}

.footer__soc__text {
  font-size: 15px;
  line-height: 24px;
  color: #F4F7FF;
}

.footer__soc a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background-color: #F4F7FF;
  border-radius: 100%;
  color: #262A39;
  transition: 0.3s all;
}

.footer__soc a:hover {
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.4);
}

@media (min-width: 1500px) {
  .footer__wrap__bot {
    margin-right: -35px;
    margin-left: -35px;
  }
}
@media (min-width: 1560px) {
  .footer__wrap__bot {
    margin-right: -75px;
    margin-left: -75px;
  }
}
@media (max-width: 991px) {
  .footer__right__nav .f-col {
    margin-left: 50px;
  }

  .footer {
    padding-top: 75px;
  }
}
@media (max-width: 767px) {
  .footer__wrap {
    flex-direction: column;
    align-items: center;
  }

  .footer__left {
    margin-bottom: 25px;
    width: 100%;
  }

  .footer__right__nav .f-col {
    margin-left: 0;
    margin-right: 25px;
  }

  .footer__right__nav li {
    margin-bottom: 10px;
  }

  .footer__right__nav .col:last-child {
    margin-right: 0;
  }

  .footer__right {
    width: 100%;
  }

  .footer__wrap__bot {
    flex-direction: column;
  }

  .footer__copy {
    padding-right: 0;
    text-align: center;
    margin-bottom: 25px;
  }

  .footer__soc {
    flex-direction: column;
  }

  .footer__soc__text {
    margin-bottom: 25px;
  }

  .footer__soc li {
    margin: 0 10px;
  }

  .footer {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .footer__wrap {
    padding-bottom: 25px;
  }

  .footer {
    margin-top: 50px;
  }
}
@media (max-width: 374px) {
  .footer__right__nav .f-col {
    margin-right: 15px;
  }
}
.status {
  max-width: 1062px;
  margin-left: auto;
  padding: 0 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #0A243F;
  margin-bottom: 37px;
}

@media (max-width: 1450px) {
  .status {
    max-width: 955px;
  }
}
@media (max-width: 1400px) {
  .status {
    max-width: 975px;
  }
}
.status__left {
  display: flex;
  align-items: center;
}

.status__type {
  display: flex;
  align-items: center;
  margin-left: 20px;
  color: #0F47C0;
}
.status__type img {
  margin-right: 2px;
}

.status__right {
  display: flex;
  align-items: center;
}
.status__right .form-switch {
  margin-left: 20px;
}

.form-check-label, .form-check-input {
  cursor: pointer;
}

.status__left .tooltips-wrap .tooltips {
  font-family: "Open Sans", sans-serif;
  width: 395px;
  bottom: calc(100% + 0px);
  left: auto;
  transform: translateX(30px);
  right: 100%;
  font-size: 14px;
  font-weight: 400;
  padding: 5px 8px;
  letter-spacing: 0;
}
.status__left .tooltips-wrap .tooltips a {
  text-decoration: underline;
  color: #9ABDFF;
  font-weight: 700;
}
.status__left .tooltips-wrap .tooltips a:hover {
  opacity: 0.9;
}
.status__left .tooltips-wrap .tooltips:before {
  left: auto;
  right: 15px;
}
.status__left .tooltips-wrap .tooltips:after {
  content: "";
  left: 100%;
  position: absolute;
  height: 100%;
  width: 100px;
  display: block;
  top: 0;
}

@media (max-width: 1350px) {
  .status__left .tooltips-wrap .tooltips {
    right: auto;
    left: 0;
    width: 250px;
    transform: translate(0);
  }
  .status__left .tooltips-wrap .tooltips:before {
    left: 15px;
    right: auto;
  }
  .status__left .tooltips-wrap .tooltips:after {
    display: none;
  }
}
@media (max-width: 480px) {
  .status__left .tooltips-wrap .tooltips {
    right: 0;
    left: auto;
    top: 100%;
    bottom: auto;
    width: 250px;
  }
  .status__left .tooltips-wrap .tooltips:before {
    right: 15px;
    left: auto;
    bottom: auto;
    top: -8px;
  }
}
.form-switch {
  padding-left: 3em;
}
.form-switch .form-check-input {
  width: 2.5em;
  margin-left: -3em;
  margin-top: 0.15em;
}
.form-switch .form-check-input:checked {
  background-color: #0F47C0;
  border-color: #0F47C0;
}
.form-switch .form-check-input {
  height: 1.4em;
}

@media (max-width: 991px) {
  .status {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .status {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 15px;
  }

  .status__left {
    margin-bottom: 20px;
  }
}
@media (max-width: 374px) {
  .status__right {
    flex-direction: column;
  }

  .status__right .form-switch {
    margin-top: 15px;
    margin-left: 0;
  }
}
.exp-content {
  display: flex;
}

.exp-content__sidebar {
  flex: 0 0 247px;
  margin-right: 63px;
}

.exp-content__edit {
  flex: 1;
  padding: 12px;
  background-color: #fff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.sidebar__img {
  width: 230px;
  height: 230px;
  border-radius: 100%;
  overflow: hidden;
  border: 6px solid #FFFFFF;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  transition: 0.3s all;
  margin-bottom: 24px;
}

.sidebar__img:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}

.sidebar {
  margin-top: 42px;
