.about__sett {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 22px;
  margin-bottom: 22px;
}

.about__sett__left {
  display: flex;
  align-items: center;
}

.custom-select {
  font-family: inherit;
}

.about__sett__color {
  font-size: 12px;
  line-height: 12px;
  font-weight: bold;
  padding: 5px 1px;
  color: #FFFFFF;
  border-radius: 2px;
  margin-right: 4px;
}

.about__sett__right {
  width: 250px;
}

.select-selected {
  background: #FFFFFF;
  border: 1px solid #A1A4B1;
  box-sizing: border-box;
  border-radius: 20px;
  font-size: 14px;
  line-height: 24px;
  color: #8B90A0;
  padding: 11px 16px;
  padding-right: 40px;
}

.select-selected:after {
  border-color: #A1A4B1 transparent transparent transparent;
  top: 21px;
  right: 23px;
}

.select-selected.select-arrow-active:after {
  border-color: transparent transparent #A1A4B1 transparent;
  top: 16px;
}

.select-selected.select-arrow-active {
  border: 1px solid #2B5CFB;
  box-sizing: border-box;
  /* Shadow/Level1 */
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 20px 20px 0px 0px;
  color: rgba(0, 0, 0, 0.8);
}

.select-items {
  border: 1px solid #2B5CFB;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 0px 0px 4px 4px;
  background: #FFFFFF;
}

.select-items div {
  font-size: 14px;
  line-height: 24px;
  color: #8B90A0;
}

.select-items div:hover, .select-items .same-as-selected {
  color: #092C9E;
}

.select-items div:hover {
  background-color: #fff;
}

.select-items div:hover, .same-as-selected {
