.tooltips-wrap {
  position: relative;
  cursor: pointer;
}
.tooltips-wrap .tooltips {
  display: none;
  bottom: calc(100% + 25px);
  left: 50%;
  transform: translateX(-50%);
  width: 215px;
}

.tooltips-wrap:hover .tooltips {
  display: block;
}

.tooltips {
  position: absolute;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  padding: 5px 8px;
  background: #000000;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}

.tooltips:before {
  content: "";
  width: 12px;
  height: 12px;
  position: absolute;
  left: 15px;
  bottom: 0;
  background-color: #000;
  transform: rotate(45deg) translateY(50%);
